	module.exports = {"data":{"menu":{"edges":[{"node":{"id":"/home/travis/build/signalwerk/LGM-htdocs-2019/src/pages/attend.md absPath of file >>> MarkdownRemark","fields":{"slug":"/attend/"},"frontmatter":{"title":"030--Attend"}}},{"node":{"id":"/home/travis/build/signalwerk/LGM-htdocs-2019/src/pages/contact.md absPath of file >>> MarkdownRemark","fields":{"slug":"/contact/"},"frontmatter":{"title":"050--Contact"}}},{"node":{"id":"/home/travis/build/signalwerk/LGM-htdocs-2019/src/pages/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/"},"frontmatter":{"title":"010--Front"}}},{"node":{"id":"/home/travis/build/signalwerk/LGM-htdocs-2019/src/pages/lgm.md absPath of file >>> MarkdownRemark","fields":{"slug":"/lgm/"},"frontmatter":{"title":"040--LGM"}}},{"node":{"id":"/home/travis/build/signalwerk/LGM-htdocs-2019/src/pages/program.md absPath of file >>> MarkdownRemark","fields":{"slug":"/program/"},"frontmatter":{"title":"020--Program"}}}]},"site":{"siteMetadata":{"title":"LGM2019"}},"markdownRemark":{"html":"<div class=\"grid\">\n  <div class=\"col1to6\">\n    <hr>\n  </div>\n  <div class=\"col7to12\">\n    <hr class=\"thick\">\n    <hr class=\"thick\">\n  </div>\n</div>\n<h1 id=\"call-for-participation\"><a href=\"#call-for-participation\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Call for participation</h1>\n<div class=\"grid\">\n  <div class=\"col1to6\">\n    <hr class=\"thick\">\n    <hr class=\"thick\">\n  </div>\n  <div class=\"col7to12\">\n    <hr>\n  </div>\n</div>\n<p>The 2019 Libre Graphics Meeting (LGM) will take place in the summer in Saarbrücken, Germany. This annual gathering is an opportunity for teams, contributors and practitioners involved in Libre Graphics to work together, share experiences, and hear new and inspiring ideas. By libre Graphics we mean “free, Libre and Open Source tools for creative uses”. LGM is not just about software, but extends to standards and file formats used in creative work.</p>\n<p>Participants in LGM include developers, designers, academics, and activists. People from around the world who are passionate about Free/Libre tools and their creative applications.</p>\n<h3 id=\"main-programme\"><a href=\"#main-programme\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Main programme</h3>\n<p>We are looking for:</p>\n<ul>\n<li>Reflections and practical sessions on promoting the philosophy and use of Libre Graphics tools.</li>\n<li>Technical presentations and workshops for developers.</li>\n<li>Showcases of excellent work made using Libre Graphics tools.</li>\n<li>New tools and workflows for graphics and code.</li>\n<li>Reflections on the activities of existing Free/Libre and Open Source communities.</li>\n</ul>\n<h3 id=\"formats\"><a href=\"#formats\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Formats</h3>\n<p>Available formats (times include Q&#x26;A):</p>\n<ul>\n<li>Entry for State of Libre Graphics (1-2 slides)</li>\n<li>Presentations (20 minutes)</li>\n<li>Birds of a Feather (BoF), Workshops, Hackathons (1-2 hours)</li>\n<li>Lightning talks (5 minutes, selected at the event unconference style)</li>\n</ul>\n<h3 id=\"state-of-libre-graphics\"><a href=\"#state-of-libre-graphics\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>State of Libre Graphics</h3>\n<p>Each meeting begins with a joint session that brings together everything that’s happened in our wide landscape over the last year. Instead of slots for general updates on the various software projects, we invite every team to submit two slides (maximum) with a short explanatory text that showcases the progress made over the past year.</p>\n<p>For first-time presenters, and particularly big or new topics in established projects, we encourage additional stand-alone submissions.</p>\n<h2 id=\"day-1\"><a href=\"#day-1\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Day 1</h2>\n<table>\n  <tr>\n    <th class=\"w30p\">Time</th>\n    <th class=\"w70p\">Talk</th>\n  </tr>\n  <tr>\n    <td>00:00</td>\n    <td><strong>Welcome to LGM</strong></td>\n  </tr>\n  <tr>\n    <td>00:00</td>\n    <td><strong>State of Libre Graphics</strong></td>\n  </tr>\n  <tr>\n    <td>00:00</td>\n    <td><strong>Organizational meetings</strong></td>\n  </tr>\n  <tr>\n    <td>00:00</td>\n    <td><strong>to be announced...</strong></td>\n  </tr>\n</table>\n<h2 id=\"day-2\"><a href=\"#day-2\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Day 2</h2>\n<table>\n  <tr>\n    <th class=\"w30p\">Time</th>\n    <th class=\"w70p\">Talk</th>\n  </tr>\n  <tr>\n    <td>00:00</td>\n    <td><strong>to be announced...</strong></td>\n  </tr>\n</table>\n<h2 id=\"day-3\"><a href=\"#day-3\" aria-hidden=\"true\" class=\"anchor\"><svg aria-hidden=\"true\" height=\"16\" version=\"1.1\" viewBox=\"0 0 16 16\" width=\"16\"><path fill-rule=\"evenodd\" d=\"M4 9h1v1H4c-1.5 0-3-1.69-3-3.5S2.55 3 4 3h4c1.45 0 3 1.69 3 3.5 0 1.41-.91 2.72-2 3.25V8.59c.58-.45 1-1.27 1-2.09C10 5.22 8.98 4 8 4H4c-.98 0-2 1.22-2 2.5S3 9 4 9zm9-3h-1v1h1c1 0 2 1.22 2 2.5S13.98 12 13 12H9c-.98 0-2-1.22-2-2.5 0-.83.42-1.64 1-2.09V6.25c-1.09.53-2 1.84-2 3.25C6 11.31 7.55 13 9 13h4c1.45 0 3-1.69 3-3.5S14.5 6 13 6z\"></path></svg></a>Day 3</h2>\n<table>\n  <tr>\n    <th class=\"w30p\">Time</th>\n    <th class=\"w70p\">Talk</th>\n  </tr>\n  <tr>\n    <td>00:00</td>\n    <td><strong>to be announced...</strong></td>\n  </tr>\n</table>","fields":{"slug":"/program/"},"frontmatter":{"date":"May 01, 2018","title":"020--Program","style":"normal"}}},"pathContext":{"slug":"/program/"}}

